import React, { ElementType } from 'react';

const PlaceholderIcon = (): JSX.Element => <span />;

interface ItemIconProps {
  /** Это подменю? */
  isSubMenu: boolean;

  /** На этом уровне есть пункты меню с иконками? */
  withIcons?: boolean;

  /* Иконка для этого пункта меню */
  Icon?: ElementType;
}

/** Получить иконку / заполнитель иконки для пункта меню */
export function getItemIcon({
  isSubMenu,
  withIcons,
  Icon
}: ItemIconProps): ElementType | undefined {
  const showSpacerIcon = !Icon && (isSubMenu || withIcons);

  return showSpacerIcon ? PlaceholderIcon : Icon;
}
